@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
*{
    /*reset comportamento padrão do HTML*/
    margin: 0;
    /*padding: 0;*/
    outline: 0;
    box-sizing: border-box;

}
/*coloca 100% largura e altura da tela*/
html, body, #root {
    height: 100%;
}

body {
    background:#13101b ;
    background-image: url(/sevengalaxies-app/static/media/Background-2.b76f9eb8.jpg);
    background-size: auto 100%;
    background-position: 50% 50%;
    
    /*deixa a font mais legivel em brower que utiliza webkit*/
    -webkit-font-smoothing: antialiased; 
}
body, input, button {
    font-family: Roboto,sans-serif;
}

.App-header {
  /*background-color: #282c34;*/
  /*min-height: 100vh;*/

  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 1px 10px 18px 7px #000;
}
.App-header img{
  width: calc(30vmin);
}
.App main {
  max-width: 1200px;
  margin: 0 auto;
  padding: 30px 30px;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-row-gap: calc(10px + 2vmin);
  row-gap: calc(10px + 2vmin);
  text-align: center;
  font-size: calc(15px + 2vmin);
  color: white;

}
.Reserva {
  display: grid;
  justify-content: center;
  grid-template-rows : 1fr 2fr 1.5fr ;
  width: calc(300px + 20vmin);
  border-style: solid;
  border-width: calc(1px + 0.7vmin);
  border-color: #c7c3cf;
  border-radius: 2px;
  grid-row-gap: 30px;
  row-gap: 30px;
}
.Reserva button{
  border: none;
  background-color:transparent;
  color: #c7c3cf;
}
.Reserva-movel{
  display: grid;
  justify-content: center;
  grid-template-columns: 1fr 2fr 1fr ;
  width: calc(100px + 20vmin);
  grid-column-gap: 20px;
  -webkit-column-gap: 20px;
          column-gap: 20px;
  font-size: calc(40px + 5vmin);
  align-items: center;
}
.Reserva-movel button{
  font-size: calc(50px + 3vmin);
}
.Reserva-conquista{
  display: grid;
  grid-template-columns: repeat(3,1fr);
  border-style: solid solid none solid;
  grid-column-gap: 5px;
  -webkit-column-gap: 5px;
          column-gap: 5px;
  font-size: calc(20px + 3vmin);
  text-align: center;
  align-items: center;
}
.Reserva-conquista button{
  font-size: calc(20px + 3vmin);
}

/*deixar responsivo*/

@media (max-width: 1000px) {
    #app {
        flex-direction: column;
    }
    #app main {
        margin-top: 30px;
        margin-left: 0;
    }
    
    #app aside {
        width: 100%;
    }
}
@media (max-height: 750px){
  .App main{
    flex-direction: row;
    grid-column-gap: calc(10px + 2vmin);
    -webkit-column-gap: calc(10px + 2vmin);
            column-gap: calc(10px + 2vmin);
  }

}
